<template>
  <div class="wrapper">
    <div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
import Bscroll from "better-scroll";
export default {
  name: "scroll",
  mounted() {
    //Better-Scroll
    // wrapper
    this.scroll = new Bscroll(".wrapper", {
      click: true, //
      probeType: 3,
      pullUpLoad: true, //
      // bounce: false
    });
    // this.scroll.on("scroll", position => {
    //   console.log("", position.y);
    //   this.$emit("onScroll", position);
    // });
    this.scroll.on("pullingUp", () => {
      console.log("");
      //
      // this.scroll.scrollTo(0,0,1000) //
      this.scroll.finishPullUp(); // 
    });
  },
  methods: {
    refresh() {
      this.scroll.refresh();
    }
  },
  data() {
    return {
      scroll: {}
    };
  }
};
</script>

